@tailwind base;
@tailwind components;
@tailwind utilities;


.body-of-project {
    position: relative;
    background-image: url("/public/background project 1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: antiquewhite;
    min-width: fit-content;
}

#welcome-h1 {
    font-size: 80px;
}

#welcome-text {
    font-size: 30px;
}

.card-header #file {
    background-color: rgba(255, 255, 255, 0.1);
    color: aliceblue;
}

#drop-area {
    height: 200px;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
    cursor: pointer;
}

#form-container {
    padding: 50px;
}

#form-container div {
    padding: 20px;
}

#form-container input {
    width: 308px;
}

form#form strong {
    color: #ff0000;
    font-size: 15px;
}

#drop-area i {
    font-size: 48px;
}
.navbar-toggler:not(.collapsed) ~ .navbar-collapse {
    display: block;
  }

nav#navigation div#navigation-header {
    background-color: rgba(25, 46, 50, 0.7);
    border-radius: 15px;
}

#navigation ul#left-side li {
    border-radius: 10px;
    border-color: #192E32;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
    text-decoration: none;
}

#navigation ul li a {
    text-decoration: none;
    color: aliceblue;
    border-right: 3px solid aliceblue;
    border-radius: 15px;
}

#navigation-record .nav-link {
    border-radius: 10px;
    border-color: #192E32;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
    text-decoration: none;
}

#navigation-record ul li a {
    text-decoration: none;
    color: aliceblue;
    border-right: 1px solid aliceblue;
}

#languageSelector {
    border-radius: 10px;
}

#records tr {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
}

#records tr td {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    
    
}

#accidents tr {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
}

#accidents tr td {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    
}

tbody#drugAlcohol tr td {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    
}

#drugAlcohol tr {
    background-color: rgba(25, 46, 50, 0.7);
}

.table {
    border-radius: 8px;
    overflow: hidden;
    background-color: rgba(25, 46, 50, 0.7);
    
}

#employer tr {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
}

#employer tr td {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
}

tbody#employeeInfo tr td {
    display: flex;
    flex-direction: row;
    background-color: rgba(25, 46, 50, 0.7);
    
}

#employeeInfo tr {
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
}

#languageSelectorDiv {
    padding-left: 20px;
    padding-right: 20px;
    color: black;
}

footer #footer {
    bottom: 0;
    width: 100%;
    background-color: rgba(25, 46, 50, 0.7);
}

footer #footer #header {
    background-color: rgba(22, 42, 44, 0.7);
}

form#form div#fields {
    background-color: rgba(25, 46, 50, 0.7);
    color: aliceblue;
    border: none;
    margin-top: 20px;
}

div.ml-auto ul {
    border-bottom: none;
}

#register-login-logout ul a {
    background-color: rgba(40, 67, 135, 0.7);
}

#username {
    padding-right: 20px;
}

nav#navigation ul li#image a {
    border: none;
    width: 300px;
}

div#release-fields {
    background-color: rgba(25, 46, 50, 0.7);
    border-radius: 8px;
    padding-top: 20px;
}

div#release-fields input {
    width: 367px;
    padding: 8px 16px;
    font-size: 1.25rem;
    border-radius: 8px;
}

div#release-fields label {
    font-size: 1.25rem;
    font-weight: bold;
}

div#policy-container {
    background-color: rgba(22, 42, 44, 0.7);
    border-radius: 15px;
}

main#about-us-main {
    background-color: rgba(25, 46, 50, 0.7);
}

main#about-us-main h2 {
    font-size: 80px;
}

main#about-us-main p:not(#headerText) {
    font-size: 20px;
}

main#about-us-main li {
    font-size: 20px;
}

main#about-us-main h3 {
    font-size: 40px;
}

main#get-hired-main {
    background-color: rgba(25, 46, 50, 0.7);
    
}

main#get-hired-main h1 {
    font-size: 80px;
}

main#get-hired-main h2 {
    font-size: 40px;
}

main#get-hired-main h3 {
    font-size: 40px;
}

main#get-hired-main h4 {
    font-size: 40px;
}

main#get-hired-main p:not(#topText) {
    font-size: 20px;
}

main#get-hired-main li {
    font-size: 20px;
}

#print-button {
    margin: 20px 0;
    width: 100px;
}
@media print {
    .print-hide {
      display: none !important;
    }
  
    .print-show {
      display: block !important;
    }
  }

.register-form {
    background-color: rgba(25, 46, 50, 0.7);
    min-height: 100%;
}

