@tailwind base;
@tailwind components;
@tailwind utilities;

.custom-button {
  background: linear-gradient(45deg, transparent, transparent 8%, rgba(255, 127, 80, 0.4) 50%, transparent 50%, transparent);
  background-size: 200% 200%;
  background-color: rgba(255, 127, 80, 0.4);
  color: rgb(251, 150, 95);
  font-weight: 700;
  animation: darkLine 5s linear infinite;
  font-variant-caps: all-small-caps;
}

.custom-button-projects {
  background: linear-gradient(45deg, transparent, transparent 8%, rgba(255, 127, 80, 0.4) 50%, transparent 50%, transparent);
  background-size: 200% 200%;
  background-color: rgba(75, 44, 32, 0.4);
  color: rgb(249, 193, 163);
  font-weight: bold;
font-variant-caps: all-small-caps;
  animation: darkLine 5s linear infinite;
}

.custom-color {
  background-color: rgba(255, 127, 80, 0.4);
}

.App {
  overflow: hidden;
  scroll-snap-align: start;
  height: 100vh;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  overflow: hidden;
  
}


#hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.circle {
  position: fixed;
  width: 800px;
  height: 800px;
  background: radial-gradient(circle, #00ddff -100%, transparent 30%);
  border-radius: 50%;
  pointer-events: none;
  transform: translate(-50%, -50%);
  z-index: 9999;
}

@keyframes darkLine {
  100% {
    background-position: 0% 100%;
  }
  0% {
    background-position: 100% 0%;
  }
}

#aboutTitle,
#workExperienceTitle,
#educationTitle {
  background: linear-gradient(to right, hsl(17, 100%, 90%) 0, hsl(17, 100%, 40%) 30%, hsl(17, 100%, 100%) 100%);
  background-clip: text;
  background-size: 200% 100%;
  -webkit-text-fill-color: transparent;
  animation: darkLine 5s linear infinite;
}
#workexperience {
  overflow: hidden;
  position: relative;
}

.link-with-fading-border {
  position: relative;
  display: inline-block;
}

.link-with-fading-border::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2px;
  height: 2px;
  transition: all 0.3s ease-in-out;
}

.link-with-fading-border.border-active::after {
  background: linear-gradient(to right, rgba(255, 127, 80, 0), #ff7f50, #ff7f50 70%, rgba(255, 127, 80, 0));
}
#projects {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 100vh;
} 

.list {
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 7xl;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list li {
  width: 80vw;
  height: 60vh;
  opacity: 0.25; 
  position: absolute;
  left: 50%;
  margin-left: -40vw;
  transition: transform 1s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.list .act.center { 
  opacity: 1;
  transform: translateX(0);
  z-index: 2;
}

.list .act.center:hover .project-title { 
  opacity: 1;
  
}

.list .prev,
.list .next {
  cursor: pointer;
  opacity: 0.5; 
  z-index: 0;
}

.list li:hover {

  background-color: rgba(0, 0, 0, 0.8);
}

.list .project-title {
  opacity: 0;
  transition: opacity 0.3s ease;
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 10px 0;
  z-index: 2;
}

.list li:hover .project-title {
  opacity: 1;
}

.list .act {
  opacity: 1;
  transform: translateX(0) scale(1);
}

.list .prev,
.list .next {
  cursor: pointer;
}

.list .prev {
  transform: translateX(-50vw) scale(0.85);
}

.list .next {
  transform: translateX(50vw) scale(0.85);
}

.list .hide {
  transform: translateX(-100vw) scale(0.85);
}

.list .new-next {
  transform: translateX(100vw) scale(0.85);
}

.list .hide,
.list .new-next {
  opacity: 0;
  transition: opacity 0.5s, transform 0.5s;
}

.custom-button:hover {
  background-color: rgb(224, 96, 50);

}
@media (min-width: 640px) {
  .list li {
    width: 60vw;
    height: 50vh;
    margin-left: -30vw;
  }
  .list .prev {
    transform: translateX(-30vw) scale(0.85);
  }
  .list .next {
    transform: translateX(30vw) scale(0.85);
  }
}

@media (min-width: 768px) {
  .list li {
    width: 40vw;
    height: 40vh;
    margin-left: -20vw;
  }
  .list .prev {
    transform: translateX(-20vw) scale(0.85);
  }
  .list .next {
    transform: translateX(20vw) scale(0.85);
  }
}

@media (min-width: 1024px) {
  .list li {
    width: 40vw;
    height: 40vh;
    margin-left: -20vw;
  }
  .list .prev {
    transform: translateX(-30vw) scale(0.85);
  }
  .list .next {
    transform: translateX(30vw) scale(0.85);
  }
}

.nav-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0.5rem;
  color: rgb(46, 41, 41);
  font-size: 2em;
  font-weight: 700;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 10;
  width: 80px;
  height: 80px;
}

.nav-arrow:hover{
  background-color: rgb(224, 96, 50);
}

.left-arrow {
  left: 10rem;
  border-style: solid;
  border-color: rgba(224, 96, 50, 0.4);
  background-color: rgba(255, 127, 80, 0.6);
  box-shadow: 0 0 10px rgba(224, 96, 50, 0.6);
}

.right-arrow {
  right: 10rem;
  border-style: solid;
  border-color: rgba(224, 96, 50, 0.4);
  background-color: rgba(255, 127, 80, 0.6);
  box-shadow: 0 0 10px rgba(224, 96, 50, 0.6);
}

@media (min-width: 300px) {
  .left-arrow {
   left: 2rem;

  }
  .right-arrow{
    right: 2rem;
  }
  .pagination-circle{
    margin-top: 150px;
  }
 
}

  @media (min-width: 768px) {
    .left-arrow {
      left: 3rem;
     }
     .right-arrow{
       right: 3rem;
     }
  }

  @media (min-width: 1024px) {
    .left-arrow {
      left: 2rem;
     }
     .right-arrow{
       right: 2rem;
     }
  }


.slidable-indicator {
  position: absolute;
  bottom: 12rem;  
  left: 0;
  right: 0;
  display: flex;
  height: 40px;
  z-index: 5;
}

.pagination-circle {
  width: 20px;
  height: 20px;
  background-color: #ddd;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 5 px;
}

.pagination-circle.active {
  background-color: rgba(224, 96, 50);
}

.fading-border {
  position: relative;
  font-weight: 700;
  overflow: hidden;
  color: rgb(255, 140, 80);
}

.fading-border::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(to right, rgba(255, 127, 80, 0), rgba(255, 127, 80, 1), rgba(255, 127, 80, 0));
  pointer-events: none;
}
.border-color {
  border-width: 2px;
  border-color: rgba(255, 127, 80);
}


.loading-background {
  background: rgb(61,123,230);
}

.spinner-border {
  border-top-color: rgb(255, 140, 80);
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}

.loading-text span {
  animation: rotateText 2s linear infinite;
}

@keyframes rotateText {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
@keyframes float {
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-10px);
  }
  100% {
    transform: translatey(0px);
  }
}

.floating {
  animation: float 3s ease-in-out infinite;
}

